/* Post Card Styling */
.post-card {
    width: 20vmax;
    /* border: 1px solid #e1e4e8; */
    border-radius: 8px;
    margin: 10px;
    padding: 16px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-sizing: border-box; /* Add box-sizing to include padding in height */
    transition: transform 0.3s ease;
    position: relative;
    background: white;
    box-sizing: border-box;
}

.post-card:hover {
    transform: scale(1.15);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}
.post-card img {
    width: 20vmax;
    height: 10vmax; /* Set a fixed height for the image */
    object-fit: contain; /* Cover the image area without distortion */
    border-bottom: 1px solid #e1e4e8;
    padding-bottom: 16px;
}

.post-card h3 {
    font-size: 1vmax;
    margin: 16px 0 0 0;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2; /* Number of lines to show */
    -webkit-box-orient: vertical;
    line-height: 1.5em; /* Adjust as necessary */
    max-height: 3em; /* 2 lines * line-height */
    height: 3em; /* Ensure title space is consistent */
}

.scroll-container {
    display: flex;
    overflow-x: auto;
    gap: 10px;
    padding-bottom: 16px;
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
    scrollbar-width: none; 
}

.post-card a {
    color: #007bff;
    text-decoration: none;
    font-weight: bold;
    display: flex;
    flex-direction: column;
    justify-content: space-between; /* Ensure proper spacing in the link */
    height: 100%; /* Ensure link height covers the card */
}

.load-more-button {
    display: inline-block;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    cursor: pointer;
    font-size: 1.5rem;
    transition: all 0.3s ease;
}



/* Responsive Design */
@media (max-width: 768px) {
    .post-card {
        width: 200px;
    }
}

@media (max-width: 480px) {
    .post-card {
        width: 150px;
    }
}
